@import '@/css/base/index.scss';

.HireSection {
  
}

.content {
  display: flex;
}

.img-container {
  position: relative;
  flex-basis: 40%;
  height: 300px;
  overflow: hidden;
}

.img {
  position: absolute;
  right: 0;
  top: 30px;
  width: 150%;
}

.reasons {
  @include center;
  // @include border;
  align-items: flex-start;
  flex-basis: 60%;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.reason {
  // @include border;
  display: flex;
  font-size: 22px;
  color: rgba(0, 0, 0, 0.705);
}

.icon-fire {
  margin-right: 15px;
  font-size: 30px;
  color: rgb(238, 255, 0);
  vertical-align: middle;
  white-space: pre-wrap;
}

.reason-text {
  flex-grow: 1;
}

@media screen and (max-width: 678px) {
  .img-container {
    display: none;
  }
  .content {
    // @include border;
    justify-content: center;
  }
  .reasons {
    flex-basis: initial;
  }
}

@media screen and (max-width: 461px) {
  .reasons {
    padding: 0;
  }
  .reason {
    line-height: 1;
    margin-top: 10px;
    font-size: 18px;
  }
}
