@mixin border {
  border: 1px solid rgb(0, 0, 0);
}

@mixin center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin theme-background($prefix) {
  #{$prefix}.theme-1 {
    background-color: $theme-1;
  }
  #{$prefix}.theme-2 {
    background-color: $theme-2;
  }
  #{$prefix}.theme-3 {
    background-color: $theme-3;
  }
  #{$prefix}.theme-4 {
    background-color: $theme-4;
  }
  #{$prefix}.theme-5 {
    background-color: $theme-5;
  }
  #{$prefix}.theme-6 {
    background-color: $theme-6;
  }
  #{$prefix}.theme-7 {
    background-color: $theme-7;
  }
  #{$prefix}.theme-8 {
    background-color: $theme-8;
  }
}

@mixin theme-color($prefix) {
  #{$prefix}.theme-1 {
    color: $theme-1;
  }
  #{$prefix}.theme-2 {
    color: $theme-2;
  }
  #{$prefix}.theme-3 {
    color: $theme-3;
  }
  #{$prefix}.theme-4 {
    color: $theme-4;
  }
  #{$prefix}.theme-5 {
    color: $theme-5;
  }
  #{$prefix}.theme-6 {
    color: $theme-6;
  }
  #{$prefix}.theme-7 {
    color: $theme-7;
  }
  #{$prefix}.theme-8 {
    color: $theme-8;
  }
}

@mixin theme-border($prefix) {
  #{$prefix}.theme-1 {
    border-color: $theme-1;
  }
  #{$prefix}.theme-2 {
    border-color: $theme-2;
  }
  #{$prefix}.theme-3 {
    border-color: $theme-3;
  }
  #{$prefix}.theme-4 {
    border-color: $theme-4;
  }
  #{$prefix}.theme-5 {
    border-color: $theme-5;
  }
  #{$prefix}.theme-6 {
    border-color: $theme-6;
  }
  #{$prefix}.theme-7 {
    border-color: $theme-7;
  }
  #{$prefix}.theme-8 {
    border-color: $theme-8;
  }
}
