@import '@base';

$color: rgb(9, 92, 187);

.MessageBtn {
  @include center;
  position: relative;
  cursor: pointer;
  position: fixed;
  bottom: 30px;
  right: 30px;
  border-radius: 50%;
  font-size: 80px;
  width: 1em;
  height: 1em;
  color: $color;
  background-color: white;

  &::after {
    $color: rgb(55, 84, 180);

    content: '';
    border: 3px solid $color;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    animation: {
      name: move;
      duration: 1s;
      iteration-count: infinite;
      timing-function: linear;
    }
  }
}

@keyframes move {
  from {
    width: 100%;
    height: 100%;
  }
  to {
    width: 130%;
    height: 130%;
  }
}
