@import '@base';

.Project {
  // @include border;
  display: flex;
  align-items: center;
  margin-top: 50px;

  &:nth-child(1) {
    margin-top: 0;
  }
}

.img-container {
  // @include border;
  flex-basis: 50%;
  padding: 30px;
}

.img {
  width: 100%;
  border-radius: 15px;
  cursor: pointer;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.336);

  &:hover {
    transform: scale(1.1);
  }
}

.body {
  // @include border;
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {

}

.text {
  // @include border;
  text-align: center;
  margin-top: 30px;
  width: 60%;
  // white-space: pre-line;
}

.buttons {
  display: flex;
  justify-content: space-between;
}

%button {
  margin-top: 20px;
  padding: 5px 10px 5px 10px;
  font-size: 18px;
  text-align: center;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  color: white;
  border: 2px solid transparent;
  font-size: 15px;
  font-weight: 600;
}

$btn-1-color: rgb(1, 15, 204);
$btn-2-color: rgb(241, 39, 3);

.view-code-btn {
  @extend %button;
  margin-left: 10px;
  border-color: $btn-1-color;
  color: $btn-1-color;

  &:hover {
    background-color: rgba(1, 15, 204, 0.158);
  }
}

.view-site-btn {
  @extend %button;
  border-color: $btn-2-color;
  color: $btn-2-color;

  &:hover {
    background-color: rgba(241, 39, 3, 0.158);
  }
}

%icon {
  font-size: 20px;
}

.icon-container {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: yellow;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}

.icon-code {
  @extend %icon;
  color: $btn-1-color;
}

.icon-site {
  @extend %icon;
  color: $btn-2-color;
}

@media screen and (max-width: 686px) {
  .Project {
    flex-direction: column;
    align-items: initial;
    margin-left: auto;
    margin-right: auto;
    width: 70%;
  }
  .img-container {
    padding: 0;
  }
  .body {
    align-items: flex-start;
    padding: 20px;
  }
  .text {
    width: 100%;
    text-align: left;
    margin-top: 0;
  }
}

@media screen and (max-width: 459px) {
  .Project {
    width: 100%;
  }
}
