.Loading {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgb(0, 0, 0);
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  animation: {
    delay: 1s;
    name: fade-out;
    duration: 1s;
    fill-mode: forwards;
    timing-function: linear;
  }
}

.circle {
  width: 100px;
  height: 100px;
  border: 10px solid rgb(255, 0, 0);
  border-radius: 50%;
  border-right: 10px solid black;
  animation: {
    name: spin;
    duration: 1s;
    fill-mode: forwards;
    timing-function: linear;
    iteration-count: infinite;
  }
}

@keyframes spin {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
