@import '@/css/base/index.scss';

.Settings {
  position: fixed;
  z-index: 999;
  padding: 20px 60px 20px 30px;
  background-color: white;
  top: 0;
  right: 0;
  height: 100%;
  transition: transform .2s linear;

  &.closed {
    transform: translateX(100%);
  }
  &.opened {
    transform: translateX(0);
  }
}

.title {
  font-size: 27px;
  margin-bottom: 25px;
}

.sub-title {
  font-size: 20px;
  color: rgba(0, 0, 0, 0.486);
  margin-bottom: 15px;
}

.themes {
  display: grid;
  grid-template-columns: 25px 25px 25px 25px;
  grid-gap: 15px;
  grid-template-rows: 25px 25px;
  margin-bottom: 25px;
}

.theme {
  & {
    border-radius: 50%;
    cursor: pointer;
    position: relative;
  }
  &:nth-child(1) {
    background-color: $theme-1-t;
  }
  &:nth-child(2) {
    background-color: $theme-2-t;
  }
  &:nth-child(3) {
    background-color: $theme-3-t;
  }
  &:nth-child(4) {
    background-color: $theme-4-t;
  }
  &:nth-child(5) {
    background-color: $theme-5-t;
  }
  &:nth-child(6) {
    background-color: $theme-6-t;
  }
  &:nth-child(7) {
    background-color: $theme-7-t;
  }
  &:nth-child(8) {
    background-color: $theme-8-t;
  }
}

.theme-outline {
  display: none;
  position: absolute;
  top: -5px;
  left: -5px;
  bottom: -5px;
  right: -5px;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(0, 0, 0, 0.459);
  border-radius: 50%;
}

.theme.selected {
  .theme-outline {
    display: initial;
  }
  &:nth-child(1) {
    background-color: $theme-1;
  }
  &:nth-child(2) {
    background-color: $theme-2;
  }
  &:nth-child(3) {
    background-color: $theme-3;
  }
  &:nth-child(4) {
    background-color: $theme-4;
  }
  &:nth-child(5) {
    background-color: $theme-5;
  }
  &:nth-child(6) {
    background-color: $theme-6;
  }
  &:nth-child(7) {
    background-color: $theme-7;
  }
  &:nth-child(8) {
    background-color: $theme-8;
  }
}

.languages {
  
}

.language {
  margin-top: 5px;
  position: relative;
  padding: 5px 5px 5px 10px;
  color: rgba(0, 0, 0, 0.74);
  border-radius: 20px;
  cursor: pointer;
  border: 1px solid transparent;
  user-select: none;
  font-weight: 600;
  -webkit-tap-highlight-color: transparent;

  &:nth-child(1) {
    margin-top: 0;
  }
  &:hover:not(.selected) {
    background-color: rgba(0, 0, 0, 0.103);
  }
  &.selected {
    border-color: rgb(2, 128, 2);
    color: rgb(2, 128, 2);
    background-color: rgba(2, 128, 2, 0.151);
  }
  &.selected::after {
    content: '';
    width: 10px;
    height: 10px;
    background-color: rgb(2, 128, 2);
    border-radius: 50%;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
  }
}
