@import "@/css/base/index.scss";

.wrapper {
  position: sticky;
  top: 0;
  z-index: 997;
}

.Header {
  z-index: 500;
  user-select: none;
  padding: 20px 50px 20px 100px;
  background-color: rgba(0, 0, 0, 0.897);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  position: relative;
  font-size: 25px;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.226);
}

.logo-text {
  position: relative;
  z-index: 3;
}

.logo-rect {
  border-radius: 15px;
  background-color: rgba(135, 57, 250, 1);
  position: absolute;
  right: 0;
  top: 0;
  width: 45px;
  height: 45px;
  z-index: 1;
  transform: translateY(-5px) rotate(45deg);
}

@include theme-background('.logo-rect');

.nav {
  & ul {
    display: flex;
  }
  &-link {
    & {
      color: rgba(255, 255, 255, 0.562);
      margin-left: 20px;
      position: relative;
      font-size: 18px;
    }
    &:nth-child(1) {
      margin-left: 0;
    }
    &.active {
      color: white;
    }
    &:hover {
      color: white;
    }
  }
}

.nav-bottom {
  // @include border;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.329);
  display: none;
  background-color: white;
}

.link-underline {
  transition: width .2s linear;
  width: 0%;
  background-image: linear-gradient(to right, red, yellow);
  position: absolute;
  bottom: -5px;
  left: 0;
  background-color: blue;
  height: 1px;
}

.settings {
  @include center;
  width: 50px;
  height: 50px;
  cursor: pointer;
  font-size: 25px;
  border-radius: 15px;
}

@include theme-background('.settings');

@media screen and (max-width: 662px) {
  .Header {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 505px) {
  .Header {
    padding-top: 5px;
    padding-bottom: 5px;
    overflow: hidden;
  }
  .nav:not(.nav-bottom) {
    display: none;
  }
  .nav-bottom {
    display: block;
  }
  .nav {
    ul {
      justify-content: center;
    }
    &-link {
      & {
        color: rgb(0, 0, 0);
      }
      &.active {
        color: rgba(0, 0, 0, 0.61);
      }
    }
  }
  .settings {
    height: 35px;
    width: 35px;
    font-size: 22px;
    border-radius: 3px;
  }
}
