@import '@/css/base/index.scss';

.SkillsSection {

}

.title {
  text-align: center;
}

.skills {
  display: flex;
  flex-direction: column;
}

.skill {
  & {
    margin: 15px auto 0;
    position: relative;
    display: flex;
    width: 100%;
    height: 150px;
    background-color: rgba(197, 212, 227, 0.5);
  }
  &:nth-child(1) {
    margin-top: 0;
  }
  &::after {
    content: '';
    width: 7px;
    position: absolute;
    background-color: grey;
    right: 0;
    top: 0;
    bottom: 0;
  }
  &:nth-child(1)::after {
    background-color: rgb(67, 67, 218);
  }
  &:nth-child(2)::after {
    background-color: rgb(56, 143, 15);
  }
  &:nth-child(3)::after {
    background-color: rgb(216, 30, 30);
  }
  &:nth-child(4)::after {
    background-color: rgb(175, 108, 7);
  }
  &:nth-child(5)::after {
    background-color: rgb(243, 3, 223)
  }
}

.img-container {
  height: 100%;
  width: 150px;
  background-color: rgba(128, 128, 128, 0);
  display: flex;
  justify-content: center;
  // @include border;
}

.img {
  padding: 10px;
  width: 100%;
}

.skill-body {
  // @include border;
  flex-grow: 1;
  padding: 15px 0;
  margin-left: 50px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.skill-title {
  cursor: pointer;
  text-decoration: underline;
}

.skill-header-title {
  display: none;
  text-decoration: underline;
}

.info-grey {
  color: rgba(0, 0, 0, 0.5);
}

@media screen and (max-width: 784px) {
  .skill-body {
    margin-left: 0;
  }
}

@media screen and (max-width: 649px) {
  .skill {
    flex-direction: column;
    height: initial;
  }
  .img-container {
    width: initial;
    height: initial;
    justify-content: flex-start;
  }
  .img {
    // @include border;
    width: 70px;
    height: 70px;
  }
  .skill-body {
    padding-left: 10px;
  }
  .skill-title {
    display: none;
  }
  .skill-header-title {
    // @include border;
    padding-left: 20px;
    display: flex;
    align-items: center;
  }
}
