@import '@/css/base/index.scss';

.Vote {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
}

.form {
  padding: 20px 50px;
  display: flex;
  border-radius: 20px;
  flex-direction: column;
  background-color: rgba(196, 196, 196, 0.425);
}

.stars {
  display: flex;
  justify-content: center;
}

.star {
  margin-left: 10px;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;

  &:nth-child(1) {
    margin-left: 0;
  }
}

.icon-star {
  font-size: 30px;

  &.filled {
    color: rgb(255, 255, 0);
    text-shadow: 0 0 10px rgba(255, 255, 0, 0.541);
  }
}

.input-name {
  margin-top: 70px;
  border: 1px solid transparent;
  text-indent: 15px;
  border-radius: 10px;
  font-size: 18px;
  padding: 8px 5px;

  &.error {
    border-color: rgb(145, 4, 4);
  }
}

.submit {
  cursor: pointer;
  border: none;
  border-radius: 10px;
  padding: 8px 0;
  margin-top: 10px;
  color: white;
  font-size: 18px;
  background-color: rgba(224, 97, 73, 1);
}

@media screen and (max-width: 435px) {
  .form {
    width: 100%;
  }
  .input-name {
    margin-top: 50px;
  }
}

@media screen and (max-width: 345px) {
  .form {
    padding-left: 10px;
    padding-right: 10px;
  }
}
