@import 'base/index.scss';

*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  font-family: Poppins, sans-serif;
  scroll-behavior: smooth;
}

body {
  background-color: #FBFBFF;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

h6 {
  font-size: 20px 
}

h5 {
  font-size: 17px;
}

h4 {
  font-size: 20px;
}

h3 {
  font-size: 28px;
}

h2 {
  font-size: 42px;
}

h1 {
  font-size: 50px;
}

ul {
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}

input {
  outline: none;
  border: none;
}

textarea {
  border: none;
}

textarea {
  outline: none;
  font-family: inherit;
}

button {
  background-color: inherit;
  border: none;
}
