@import '@base';

$ball-size: 70px;
$close-btn-color: rgb(238, 32, 32);

.Alert {
  width: 280px;
  position: relative;
  padding: 10px;
  padding-top: $ball-size / 2;
  position: fixed;
  top: 50%;
  left: 50%;
  background-color: rgb(255, 255, 255);
  transform: translate(-50%, -50%);
  z-index: 999;
  border-radius: 15px;
  display: none;
  flex-direction: column;
  align-items: center;

  &.visible {
    display: flex;
  }
}

.title {
  font-size: 30px;
  font-weight: bold;
}

.text {
  font-size: 18px;
  margin-top: 30px;
  white-space: pre-line;
  text-align: center;
}

.ball {
  background-color: rgb(255, 255, 255);
  width: 70px;
  height: 70px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-container {
  border-radius: 50%;
  border: 2px solid green;
  color: green;
  font-size: 26px;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-btn {
  & {
    margin-top: 60px;
    cursor: pointer;
    width: 100%;
    border: 1px solid $close-btn-color;
    color: $close-btn-color;
    border-radius: 10px;
    padding: 10px;
    font-size: 20px;
    transition: background-color .2s linear;
  }
  &:hover {
    background-color: $close-btn-color;
    color: white;
  }
}
