@import '@base';

.big-icon {
  position: absolute;
  padding: 5px;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: rgb(223, 218, 218);
}

.icon-telegram {
  font-size: 60px;
  color: rgb(25, 72, 202);
  width: 100%;
  height: 100%;
}

.MessageForm {
  display: flex;
  padding: 40px 10px 5px;
  width: 300px;
  max-width: 100%;
  border-radius: 10px;
  flex-direction: column;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgb(223, 218, 218);
  z-index: 999;
  display: none;
  z-index: 1000;

  &.visible {
    display: initial;
  }
}

.title {
  margin-bottom: 30px;
  text-align: center;
}

.input-group {
  display: flex;

  &:nth-child(1) {
    margin-top: 0;
  }
}

.input-icon {
  @include border;
  @include center;
  font-size: 20px;
}

%input {
  padding: 10px 10px;
  font-size: 16px;
  margin-top: 10px;
  width: 100%;
  border-radius: 15px;

  &:nth-child(1) {
    margin-top: 0;
  }
  &.error {
    border-color: rgb(194, 42, 42);
  }
}

.field-from {
  @extend %input;
  flex-grow: 1;
}

.field-phone {
  @extend %input;
  flex-grow: 1;
}

.field-message {
  @extend %input;
  width: 100%;
  resize: none;
}

.buttons {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
}

%btn {
  border: 2px solid transparent;
  width: 48%;
  padding: 7px 0;
  border-radius: 7px;
  font-weight: 400;
  font-size: 18px;
  cursor: pointer;
}

.send-btn {
  @extend %btn;
  $color: rgb(13, 41, 168);
  border-color: $color;
  color: $color;

  &:hover {
    background-color: rgba(42, 69, 190, 0.288);;
  }
}

.close-btn {
  @extend %btn;
  $color: rgb(172, 19, 19);
  border-color: $color;
  color: $color;

  &:hover {
    background-color: rgba(172, 19, 19, 0.281);
  }
}
