@import '@base';

$q-color: grey;

.Question {
  position: relative;
  width: 80%;
  margin-top: 40px;
  background-color: $q-color;
  border-radius: 10px;

  &:nth-child(1) {
    margin-top: 0;
  }
}

.icon-container {
  @include border;
  @include center;
  border: 3px solid white;
  background-color: $q-color;
  position: absolute;
  left: 30px;
  top: 0;
  transform: translateY(-50%);
  width: 60px;
  height: 60px;
  border-radius: 50%;
}

.icon-question {
  font-size: 20px;
}

.title {
  text-align: center;
}
