@import "@/css/base/index.scss";

.Footer {
  background-color: rgb(44, 44, 44);
  padding: 60px 0;
  color: white;
}

.sections {
  // border: 3px solid blue;
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin: 0 auto;
}

.section {
  & {
    display: flex;
    flex-direction: column;
  }
  &:nth-child(1) {
    margin-left: 0;
    align-items: center;
  }
  &-title {
    font-size: 25px;
    border-bottom: 1px solid white;
  }
  &-list {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
  }
  &-item {
    position: relative;
    color: rgba(255, 255, 255, 0.486);
    font-size: 17px;
    margin-top: 8px;
    cursor: pointer;
    transition: color .2s linear;

    &:nth-child(1) {
      margin-top: 0;
    }
    &:hover {
      color: #ffb01f;
    }
  }
}

.ball {
  & {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: white;
    box-shadow: 0 0 40px rgba(255, 255, 255, 0.54);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  &:hover .icon-up {
    transform: translateY(-5px);
  }
}

.icon-up {
  & {
    font-size: 40px;
    transition: transform .2s linear;
  }
}

.section-1-title {
  line-height: 1;
  font-size: 40px;
  color: white;
  margin-top: 30px;
}

.section-1-subtitle {
  line-height: 1;
  color: rgba(253, 253, 253, 0.432);
  font-size: 20px;
}

@include theme-color('.icon-up');

.social {
  display: flex;
  justify-content: center;
  margin-top: 100px;
}

.social-item {
  & {
    margin-left: 60px;
  }
  &:nth-child(1) {
    margin-left: 0;
  }
}

.social-img {
  & {
    width: 50px;
    height: 50px;
    cursor: pointer;
    transition: transform .2s linear;
  }
  &:hover {
    transform: translateY(-20px);
  }
}

@media screen and (max-width: 1013px) {
  .Footer {
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
  }
  .sections {
    width: 100%;
  }
  .social {
    margin-top: 20px;
  }
}

@media screen and (max-width: 696px) {
  .Footer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .social {
    margin-top: 60px;
    margin-bottom: 100px;
  }
  .sections {
    width: initial;
    flex-direction: column;
  }
  .section {
    margin-top: 30px;

    &:nth-child(1) {
      order: 4;
    }
    &:nth-child(2) {
      margin-top: 0;
    }
  }
  .section-list {
    margin-top: 10px;
  }
  .section-item {
    margin-top: 4px;
  }
}

@media screen and (max-width: 299px) {
  .section-1-title {
    font-size: 30px;
  }
  .section-1-subtitle {
    font-size: 14px;
  }
}

@media screen and (max-width: 272px) {
  .section-1-title {
    display: none;
  }
  .section-1-subtitle {
    display: none;
  }
  .social-item {
    margin-left: 10px;

    &:nth-child(1) {
      margin-left: 0;
    }
  }
  .social-img {
    width: 40px;
    height: 40px;
  }
}
