@import '@base';

.AboutSection {
  // margin-top: 30px
}

.content {
  display: flex;
  // @include border;
}

.left {
  flex-basis: 50%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.img-container {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  z-index: 200;
}

@include theme-background('.img-container');

.img {
  border: 1px solid black;
  position: absolute;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: rgb(150, 144, 144);
  bottom: 10px;
  left: 10px;
  transition: bottom .2s linear, left .2s linear;
  z-index: 200;

  &:hover {
    bottom: 25px;
    left: 25px;
  }
}

.img-title {
  margin-top: 20px;
  font-weight: 600;
}

.img-subtitle {
  margin-top: -10px;
  color: rgba(0, 0, 0, 0.486);
}

.text {
  width: 70%;
  margin-top: 20px;
  font-size: 18px;
  color: rgba(21, 20, 24, 0.788);
  text-align: center;
}

.right {
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.item {
  display: flex;
  margin-top: 20px;
  font-size: 18px;
  color: rgba(0, 0, 0, 0.774);

  &:nth-child(1) {
    margin-top: 0;
  }
}

.icon-check {
  padding: 5px;
  margin-right: 10px;
  font-size: 22px;
  vertical-align: middle;
  color: rgb(4, 114, 4);
}

.item-text {
  flex-grow: 1;
  font-weight: 600;
}

.right-image {
  width: 100%;
  position: relative;
  animation: {
    name: up-down;
    duration: 1s;
    timing-function: linear;
    iteration-count: infinite;
    direction: alternate;
  }
}

@media screen and (max-width: 614px) {
  .text {
    width: 100%;
  }
  .content {
    flex-direction: column;
  }
  .right {
    margin-top: 20px; 
  }
  .item {
    font-size: 16px;
  }
}
